				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			page:1,
			limit:20,
			status: 'more',
			keyword:'',
		};
	},
	
	onLoad(options){
		
		console.log('页面',options);
		this.sid = options.sid
		this.keyword = options.keyword
		this.calcSize();
		getApp().user_api(true);
		
		



		this.cateId = options.tid;
		this.loadCateList(options.fid,options.sid);
		// this.loadData();
	},
	onShow(){
		this.filterIndex = 0;
		this.page = 1;
		this.goodsList = []
		this.getProductList();
	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新
	onPullDownRefresh() {
		console.log('refresh');
		setTimeout(function () {
			uni.stopPullDownRefresh();
		}, 1000);
	},
