	methods: {
		
		getProductList(salesOrder,priceOrder){
			let self = this;
			let path = '/gzh/store_api/get_product_list'
			let sid1 = self.sid ? self.sid : ''
			let salesOrder1 = salesOrder ? salesOrder : ''
			let priceOrder1 = priceOrder ? priceOrder : ''
			let keyword1 = self.keyword ? self.keyword : ''
			console.log('排序',salesOrder1);
			
			let data = {
				sid:sid1,
				salesOrder:salesOrder1,
				priceOrder:priceOrder1,
				keyword:keyword1,
				page:self.page,
				limit:self.limit
			};
			getApp().http(path, data, function(res) {
				console.log('详情',res);
				uni.hideLoading();
				
				let list = res;
				if (list.length > 0) {
					console.log('总条数',list.length)
					
					
					self.goodsList = self.goodsList.concat(list);
					self.status = list.length >= self.limit ? "more" : "";
					if (self.status == "more") {
						self.page += 1;
					}
					
					
					// self.status = list.length <= self.limit ? "more" : "";
					// if (self.status == "more") {
					// 	self.page += 1;
						
					// 	self.goodsList = self.goodsList.concat(list);
					// }
				} else {
					self.status = "";
				}
			})
		},
		//筛选点击
		tabClick(index){
			let self = this;
			console.log('点击',index)
			
			self.filterIndex = index;
			
			if(index === 2){
				this.priceOrder = this.priceOrder === 1 ? 2: 1;
			}else{
				this.priceOrder = 0;
			}
			
			uni.showLoading({
				title: '正在加载'
			})
			
			if(self.filterIndex == 0){
				console.log('综合排序')
				self.page = 1;
				self.goodsList = []
				self.getProductList()
			}else if(self.filterIndex == 1){
				self.page = 1;
				self.goodsList = []
				self.getProductList('desc')
				console.log('销量')
			}else if(self.filterIndex == 2){
				console.log('价格',this.priceOrder)
				if(this.priceOrder == 1){
					self.page = 1;
					self.goodsList = []
					self.getProductList('','desc')
				}else{
					self.page = 1;
					self.goodsList = []
					self.getProductList('','asc')
				}
			}
		},
		
		
		// 到底触发
		scroAddr(){
			let self = this;
			if(self.status){
				self.getProductList();
			}
		},
		//计算右侧栏每个tab的高度等信息
